<template>
  <div class="article">
    <van-list v-model="loading" :finished="finished" finished-text="" @load="onLoadUserArticleHistory">
      <van-cell @click="$router.push({
        name:'article',
        params:{
          articleId:article.art_id.toString()
        }
      })"
      v-for="(article,index) in list" :key="index" :title="article.title" />
    </van-list>
  </div>
</template>

<script>
import { getUserArticleHistory } from '@/api/article'

export default {
  name: 'userHistory',
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      page: 1, // 
      per_page: 10 // 
    }
  },
  methods: {
    async onLoadUserArticleHistory () {
      // 1.
      const { data } = await getUserArticleHistory({
        page: this.page, // 1
        per_page: this.per_page // 
      })
      //   console.log(data)
      // 2.
      const { results } = data.data
      this.list.push(...results)
      // 3.loading
      this.loading = false
      // 4.
      if (results.length) {
        this.page++ // 
      } else {
        this.finished = true
      }
    }
  }
}
</script>

